import { InjectedCompProps, styled, SummaryPart } from 'topwrite';
import { VscLinkExternal } from '@react-icons/all-files/vsc/VscLinkExternal';
import { ReactNode } from 'react';

interface Props {
    children: ReactNode;
    part: SummaryPart;
}

export default function PartTitle({ props, Component }: InjectedCompProps<Props>) {

    let { children, part } = props;
    if (part.getMetadata('reference')) {
        children = <Container>
            <Text>{children}</Text>
            <VscLinkExternal />
        </Container>;
    }

    return <Component {...props} >
        {children}
    </Component>;
}
const Text = styled.span`
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;
  text-overflow: ellipsis;
`;

const Container = styled.div`
  display: flex;
  align-items: center;
  overflow: hidden;

  svg {
    flex-shrink: 0;
    margin-left: 5px;
  }
`;
